<template>
    <div id="good-table">
        <div class="table-title">详情列表</div>

        <!-- 表格搜索btn start -->
        <div class="search-content clearfix">
            <div class="search-left left clearfix">
                <div class="search-name">
                    <el-input v-model="searchName" placeholder="名称或者编号" class="left"></el-input>
                </div>

                <!-- 上下架下拉框选择 -->
                <el-select class="sale-status left" v-model="saleStatus" placeholder="上/下架状态">
                    <el-option v-for="item in saleStatusItem" :key="item.value" :label="item.label" :value="item.value">
                    </el-option>
                </el-select>

                <el-button class="" @click="getList">搜索</el-button>
            </div>

            <div class="search-right right" :class="{'search-noadd': !isShowAddBtn}">
                <el-button v-if="isShowAddBtn" @click="goGoodEditor">创建</el-button>
                <el-button @click="goDraft">草稿（{{draftNum}}）</el-button>
                <el-button @click="stateOn">批量上架</el-button>
                <el-button @click="stateOff">批量下架</el-button>
                <el-button class="red" @click="muDialogDeleteOpen">删除</el-button>
            </div>
        </div>
        <!-- 表格搜索btn end -->

        <!-- table start -->
        <div class="table-content">
          <el-table class="table-list" :data="list" stripe height="680" @selection-change="handleSelectionChange" @sort-change="sort">
              <el-table-column type="selection" align="center" width="38"></el-table-column>
              <el-table-column min-width="80" align="center" label="编号" sortable="true" prop="sort_food_id">
                  <template slot-scope="scope">
                      <span>{{ scope.row.food_id}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="图片">
                  <template slot-scope="scope">
                      <img v-if="scope.row.food_picture === ''" src="./images/empty_small_pic_normal.png" alt="">
                      <img v-else :src="imgbase_url + '/img_get.php?img=1&height=69&width=69&imgname=' + scope.row.food_picture" alt="菜品图片">
                  </template>
              </el-table-column>
              <el-table-column min-width="93" align="center" label="名称">
                  <template slot-scope="scope">
                      <span>{{ scope.row.food_name}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="100" align="center" label="价格">
                  <template slot-scope="scope">
                      <div v-if="scope.row.food_show_price !==''"> &yen; {{scope.row.food_show_price}}</div>
                      <div v-if="scope.row.food_price_big !==''">大： &yen; {{scope.row.food_price_big}}</div>
                      <div v-if="scope.row.food_price_middle !==''">中： &yen; {{scope.row.food_price_middle}}</div>
                      <div v-if="scope.row.food_price_small !==''">小： &yen; {{scope.row.food_price_small}}</div>
                  </template>
              </el-table-column>
              <el-table-column min-width="60" align="center" label="规格">
                  <template slot-scope="scope">
                      <span>{{ scope.row.food_unit}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="商品类别">
                  <template slot-scope="scope">
                      <span>{{ scope.row.category_name}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="当日销量" sortable="true" prop="1">
                  <template slot-scope="scope">
                      <span>{{ scope.row.food_num_day}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="周销量" sortable="true" prop="2">
                  <template slot-scope="scope">
                      <span>{{ scope.row.food_num_week}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="月销量" sortable="true" prop="3">
                  <template slot-scope="scope">
                      <span>{{ scope.row.food_num_mon}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="余量数" sortable="true" prop="4">
                  <template slot-scope="scope">
                      <!-- <span v-if="scope.row.stock_num_day === 0">99+</span> -->
                      <span v-if="scope.row.stock_num_day === 99999">99+</span>
                      <span v-else>{{ scope.row.stock_num_day}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="分享数" sortable="true" prop="sort_fd">
                  <template slot-scope="scope">
                      <span>{{ scope.row.share_num}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="收藏数" sortable="true" prop="sort_g">
                  <template slot-scope="scope">
                      <span>{{ scope.row.favorites_num}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="评论数" sortable="true" prop="sort_r">
                  <template slot-scope="scope">
                      <span>{{ scope.row.comment_num}}</span>
                  </template>
              </el-table-column>
              <el-table-column min-width="80" align="center" label="二维码">
                  <template slot-scope="scope">
                      <img :src="scope.row.codeimgurl" alt="二维码">
                  </template>
              </el-table-column>
              <el-table-column min-width="140" align="center" label="操作">
                  <template slot-scope="scope">
                      <span @click="stateSingleOff(scope.row.food_id)" 
                        v-if="scope.row.sale_off === SaleStatusCfg.YES" 
                        class="red-text" 
                        style="padding-right:10px;padding-bottom:10px;">下架</span>
                      <span @click="stateSingleOn(scope.row.food_id)" 
                      v-else class="blue-text" style="padding-right:10px;padding-bottom:10px;">上架</span>
                      <span class="blue-text" @click="openPreveiw(scope.row.food_id)">预览</span>
                      <br>
                      <span class="blue-text">
                          <router-link v-if="scope.row.type === goodType.DISH" :to="{path:'/good',query: { foodId:scope.row.food_id}}">
                              编辑
                          </router-link>
                          <router-link v-if="scope.row.type === goodType.DRINK" :to="{path:'/good/drinkeditor',query: { foodId:scope.row.food_id}}">
                              编辑
                          </router-link>
                      </span>
                      <span @click="dialogDeleteOpen(scope.row.food_id)" 
                      class="red-text" 
                      style="padding-left:10px;padding-top:10px;">删除</span>
                  </template>
              </el-table-column>
          </el-table>
          
          <div class="data-empty" v-if="isListEmpty">
            <div class="img">
              <img src="../../../assets/images/empty.png">
            </div>
            <div class="text">
              <span>暂无数据...</span>
            </div>
          
          </div>
        </div>


        <!-- table end -->

        <!-- 分页 starat -->
        <div class="pagination-container">
            <el-pagination 
            @size-change="handleSizeChange" 
            @current-change="handleCurrentChange" 
            :current-page.sync="listQuery.page" 
            :page-sizes="[10,20,40]" 
            :page-size="listQuery.limit" 
            layout="sizes, jumper, prev, pager, next" 
            :total="total">
            </el-pagination>
        </div>
        <!-- 分页 end -->

        <preview-dialog :previewVisible="previewVisible"
            :previewObj="previewObj"
            @on-close="hidePreviewDialog"
        ></preview-dialog>

                <!-- 删除提示弹窗 -->
        <el-dialog class="delete-dialog"
        :visible.sync="deleteDialogVisible"
        :show-close="false"
        size="">
          <div class="dialog-title" slot="title"></div>
          <div class="dialog-content">
            <div class="delete-tip">
              <div class="delete-img">
                <img src="./images/question.png" alt="提示图片">
              </div>
              <div class="delete-text">
                确认删除选中配件
              </div>
            </div>

              <div class="button-group clearfix">
                  <div class="ok-btn left" @click="dialogDelete">确定</div>
                  <div class="cancel-btn left" @click="dialogDeleteCancel">取消</div>
              </div>
          </div>
          
        </el-dialog>

        <!-- 批量删除提示弹窗 -->
        <el-dialog class="delete-dialog"
        :visible.sync="muDeleteDialogVisible"
        :show-close="false"
        size="">
          <div class="dialog-title" slot="title"></div>
          <div class="dialog-content">
            <div class="delete-tip">
              <div class="delete-img">
                <img src="./images/question.png" alt="提示图片">
              </div>
              <div class="delete-text">
                确认批量删除选中配件
              </div>
            </div>

              <div class="button-group clearfix">
                  <div class="ok-btn left" @click="muDialogDelete">确定</div>
                  <div class="cancel-btn left" @click="dialogDeleteCancel">取消</div>
              </div>
          </div>
          
        </el-dialog>
    </div>
</template>

<script>
import {
  SaleStatus,
  GoodType,
  FoodType,
  priceType,
  SizeUse,
  sizeSpec
} from "@/config/cfg";
import { getTableList, menuSave, getFoodEditor } from "../api";
import { GoodList } from "../index.js";
import { ls } from "@/config/pageStore";

import PreviewDialog from "../components/previewDialogEditor";

export default {
  props: ["draftNum"],
  components: {
    PreviewDialog
  },
  data() {
    return {
      //搜索
      searchName: "", //搜索名称或者编号
      saleStatus: "", //上下架状态，0上架, 1下架 ,2全部
      deleteFoodArr: [], //删除数组

      saleStatusItem: [
        {
          value: SaleStatus.ALL,
          label: SaleStatus.toString(SaleStatus.ALL)
        },
        {
          value: SaleStatus.YES,
          label: SaleStatus.toString(SaleStatus.YES)
        },
        {
          value: SaleStatus.NO,
          label: SaleStatus.toString(SaleStatus.NO)
        }
      ],

      //列表
      list: null,
      total: null, //分页总条数
      listQuery: {
        page: 1,
        limit: 10,
        importance: undefined,
        title: undefined,
        type: undefined,
        sort: "+id"
      },

      multipleSelection: [], //存放多选选中数据

      goodType: GoodType,
      SaleStatusCfg: SaleStatus,
      priceType: priceType,

      imgbase_url: imgbase_url, //图片路径

      //排序
      isSort: false,
      sortLabel: "",
      sortOrder: 1,

      //预览弹窗
      previewVisible: false,
      previewObj: {
        shopName: "",
        foodName: "",
        composition: [],
        feature: [],
        price: null,
        priceusing: "",
        priceFirst: "",
        priceSecond: "",

        previewPicture: ""
      },

      deleteDialogVisible: false, //删除提示弹窗
      deleteId: "", //删除的id

      muDeleteDialogVisible: false, //批量删除提示弹窗

      isListEmpty: true
    };
  },
  computed: {
    selectCategoryId() {
      return this.$store.state.good.selectCategoryId;
    },
    foodType() {
      return this.$store.state.good.type;
    },
    pageSize() {
      return this.$store.state.good.pageSize;
    },
    isShowAddBtn() {
      return !(this.selectCategoryId === "" || this.selectCategoryId === "0");
    }
  },
  watch: {
    selectCategoryId: function(val) {
      this.searchName = "";
      this.saleStatus = "";
      this.getList();
    }
  },
  created() {
    this.listQuery.limit = ls.getItem("GOODFORMSIZE") || 10;
    this.getList();
    // this.initPageSize();
  },
  methods: {
    initTableList() {
      this.list = this.list.map(item => {
        item["food_picture"] = (item.food_img_list || [])[0] || "";
        //二维码图片
        let codeimgurl =
          imgbase_url +
          "/img_get.php?get_food_qrcode=1&shop_id=" +
          item.shop_id +
          "&food_id=" +
          item.food_id;
        item["codeimgurl"] = codeimgurl;

        //价格
        let foodShowPrice = "";

        if (item.type === GoodType.ACCESSORY) {
          //配件价格
          foodShowPrice = item.food_price || "";
        } else {
          //菜品，酒水价格
          if ((item.food_price || {}).type === FoodType.YES) {
            foodShowPrice = ((item.food_price.price || [])[0] || {})
              .original_price;
          } else if ((item.food_price || {}).type === FoodType.NO) {
            let arr = item.food_price.price || [];
            // let priceArr = []; //存放取到价格数组

            if (arr.length === 0) {
              item["food_show_price"] = foodShowPrice;
              return item;
            }

            arr.forEach(items => {
              // if (items.is_use) {
              // priceArr.push(items.original_price);
              // }
              if (items.spec_type === sizeSpec.BIG) {
                item["food_price_big"] = items.original_price;
              }
              if (items.spec_type === sizeSpec.MIDDEL) {
                item["food_price_middle"] = items.original_price;
              }
              if (items.spec_type === sizeSpec.SMALL) {
                item["food_price_small"] = items.original_price;
              }
            });

            //只显示两个价格，排除中
            if (arr.length === 3) {
              item["food_price_middle"] = "";
            }
            // if (priceArr.length !== 0) {
            //   let min = GoodList.arrayMin(priceArr);
            //   let max = GoodList.arrayMax(priceArr);

            //   //只有一个价格时
            //   if (min === max) {
            //     foodShowPrice = min;
            //   } else {
            //     foodShowPrice = min + "/" + max;
            //   }
            // }
          }
        }

        if (item.food_price_big != 0) {
          item["food_price_big"] = item.food_price_big || "";
        }
        if (item.food_price_middle != 0) {
          item["food_price_middle"] = item.food_price_middle || "";
        }
        if (item.food_price_small != 0) {
          item["food_price_small"] = item.food_price_small || "";
        }
        item["food_show_price"] = foodShowPrice;

        return item;
      });
    },

    //拉取table数据
    getList() {
      let data = {
        foodlist: 1,
        category_id: this.selectCategoryId,
        page_size: this.listQuery.limit,
        page_no: this.listQuery.page,
        type: 1 //排除配件的数据
      };

      if (this.searchName !== "") {
        data["food_name"] = this.searchName;
      }

      if (this.saleStatus !== "" && this.saleStatus !== 2) {
        data["sale_off"] = this.saleStatus;
      }

      //是否有排序
      if (this.isSort) {
        // data[this.sortLabel] = this.sortOrder;
        data["sortby"] = this.sortLabel;
        data["sort"] = this.sortOrder;
      }

      getTableList(data, resp => {
        if (resp.ret === 0) {
          this.list = resp.data.list || [];
          this.total = resp.data.total;
          this.initTableList();

          if (this.list.length !== 0) {
            this.isListEmpty = false;
          } else {
            this.isListEmpty = true;
          }
        } else {
          console.warn("获取列表错误! ");
        }
      });
    },

    //删除，上下架成功后处理
    opearteHandler(resp) {
      if (resp.ret === 0) {
        this.getList();
        // this.$notify({
        //   title: "成功",
        //   message: "操作成功！",
        //   type: "success"
        // });
      } else {
        // console.warn("操作失败！");
        this.$notify({
          title: "成功",
          message: "操作失败！",
          type: "error"
        });
      }
    },

    //删除
    deleteFoods() {
      let data = {
        del_food: 1,
        food_id_list: this.deleteFoodArr
      };

      menuSave(data, resp => {
        if (resp.ret === 0) {
          this.getList();
          this.$notify({
            title: "成功",
            message: "操作成功！",
            type: "success"
          });
        } else {
          console.warn("操作失败！");
          this.$notify({
            title: "成功",
            message: "操作失败！",
            type: "error"
          });
        }
      });
    },

    //单个删除
    deleteFood(foodId) {
      this.deleteFoodArr = [];
      this.deleteFoodArr.push(foodId);

      this.deleteFoods();
    },

    //批量删除
    multipleDeleteFood() {
      this.deleteFoodArr = [];
      this.deleteFoodArr = this.multipleSelection.map(function(item) {
        return item.food_id;
      });
      if (this.deleteFoodArr.length === 0) {
        this.$slnotify({
          message: "请先选择要批量删除项"
        });
        return;
      }
      this.deleteFoods();
    },

    //更改状态
    stateSingleOn(foodId) {
      let foodArr = [];
      foodArr.push(foodId);

      let data = {
        sale_off_opr: 1,
        food_id_list: foodArr,
        is_sale_off: SaleStatus.YES
      };
      menuSave(data, this.opearteHandler);
    },
    stateSingleOff(foodId) {
      let foodArr = [];
      foodArr.push(foodId);

      let data = {
        sale_off_opr: 1,
        food_id_list: foodArr,
        is_sale_off: SaleStatus.NO
      };
      menuSave(data, this.opearteHandler);
    },

    //批量更改状态
    stateOn() {
      let foodArr = this.multipleSelection.map(function(item) {
        return item.food_id;
      });

      if (foodArr.length === 0) {
        this.$slnotify({
          message: "请先选择要批量上架项"
        });
        return;
      }

      let data = {
        sale_off_opr: 1,
        food_id_list: foodArr,
        is_sale_off: SaleStatus.YES
      };

      menuSave(data, this.opearteHandler);
    },
    stateOff() {
      let foodArr = this.multipleSelection.map(function(item) {
        return item.food_id;
      });

      if (foodArr.length === 0) {
        this.$slnotify({
          message: "请先选择要批量下架项"
        });
        return;
      }

      let data = {
        sale_off_opr: 1,
        food_id_list: foodArr,
        is_sale_off: SaleStatus.NO
      };
      menuSave(data, this.opearteHandler);
    },

    //分页事件处理，条数和当前页数改变触发
    handleSizeChange(val) {
      this.listQuery.limit = val;
      ls.setItem("GOODFORMSIZE", val, 30);
      this.$store.commit("CHANGE_PAGESIZE", val);
      this.getList();
    },

    //目标页
    handleCurrentChange(val) {
      this.listQuery.page = val;
      this.getList();
    },
    goGoodEditor() {
      if (this.foodType === GoodType.DISH) {
        this.$router.push({ path: "/good" });
      } else if (this.foodType === GoodType.DRINK) {
        this.$router.push({ path: "/good/drinkeditor" });
      }
    },

    //多选
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    //去草稿页面
    goDraft() {
      this.$router.push({ path: "/good/draft" });
    },
    //初始化pageSize
    initPageSize() {
      this.listQuery.limit = this.pageSize;
    },
    //排序
    sort(item) {
      this.sortLabel = item.prop;

      if (item.order === "descending") {
        this.isSort = true;
        this.sortOrder = -1;
      } else if (item.order === "ascending") {
        this.isSort = true;
        this.sortOrder = 1;
      } else {
        this.isSort = false;
      }

      this.getList();
    },
    //预览弹窗
    openPreveiw(id) {
      getFoodEditor(id, resp => {
        if (resp.ret === 0) {
          let foodinfo = resp.data.info || {};

          this.previewObj.shopName = GoodList.getShopname();
          this.previewObj.foodName = foodinfo.food_name;
          this.previewObj.composition = foodinfo.composition;
          this.previewObj.feature = foodinfo.feature;
          this.previewObj.foodPrice = foodinfo.food_price;
          this.previewObj.previewPicture =
            (foodinfo.food_img_list || [])[0] || "";

          //价格
          let priceObj = foodinfo.food_price || {};
          let using = priceObj.using || [];

          using.forEach(item => {
            if (
              item === priceType.DISCOUNT ||
              item === priceType.FESTIVAL ||
              item === priceType.VIP
            ) {
              this.previewObj.priceusing = item;
            }
          });

          if (priceObj.type === FoodType.YES) {
            // 无分量
            if (this.previewObj.priceusing !== "") {
              switch (this.previewObj.priceusing) {
                case priceType.DISCOUNT:
                  this.previewObj.priceFirst = ((priceObj.price || [])[0] || {}
                  ).discount_price;
                  this.previewObj.priceSecond = ((priceObj.price || [])[0] || {}
                  ).original_price;
                  break;
                case priceType.FESTIVAL:
                  this.previewObj.priceFirst = ((priceObj.price || [])[0] || {}
                  ).festival_price;
                  this.previewObj.priceSecond = ((priceObj.price || [])[0] || {}
                  ).original_price;
                  break;
                case priceType.VIP:
                  this.previewObj.priceFirst = ((priceObj.price || [])[0] || {}
                  ).vip_price;
                  this.previewObj.priceSecond = ((priceObj.price || [])[0] || {}
                  ).original_price;
                  break;
              }
            } else {
              this.previewObj.priceFirst = ((priceObj.price || [])[0] || {}
              ).original_price;
            }
          } else if (priceObj.type === FoodType.NO) {
            // 有分量
            let sizePrice = {};

            (priceObj.price || []).forEach(item => {
              if (item.is_use === SizeUse.YES) {
                sizePrice = item;
              }
              return;
            });

            if (this.previewObj.priceusing !== "") {
              switch (this.previewObj.priceusing) {
                case priceType.DISCOUNT:
                  this.previewObj.priceFirst = sizePrice.discount_price;
                  this.previewObj.priceSecond = sizePrice.original_price;
                  break;
                case priceType.FESTIVAL:
                  this.previewObj.priceFirst = sizePrice.festival_price;
                  this.previewObj.priceSecond = sizePrice.original_price;
                  break;
                case priceType.VIP:
                  this.previewObj.priceFirst = sizePrice.vip_price;
                  this.previewObj.priceSecond = sizePrice.original_price;
                  break;
              }
            } else {
              this.previewObj.priceFirst = sizePrice.original_price;
            }
          }

          this.previewVisible = true;
        } else {
          this.$notify({
            title: "警告",
            message: "预览失败",
            type: "warning"
          });
        }
      });
    },
    hidePreviewDialog() {
      this.previewVisible = false;
    },
    //删除弹窗
    dialogDelete() {
      this.deleteFood(this.deleteId);
      this.deleteDialogVisible = false;
    },
    dialogDeleteCancel() {
      this.deleteDialogVisible = false;
      this.muDeleteDialogVisible = false;
    },
    dialogDeleteOpen(id) {
      this.deleteId = id;
      this.deleteDialogVisible = true;
    },
    muDialogDelete() {
      this.multipleDeleteFood();
      this.muDeleteDialogVisible = false;
    },
    muDialogDeleteOpen() {
      this.muDeleteDialogVisible = true;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

#good-table {
  background: #ffffff;
  box-shadow: 0 2px 4px 0 #becaeb;
}

.table-list {
  // margin: 0 14px;
}

.table-title {
  @include wh(100%, 40px);
  @include fc(14px, #666666);
  line-height: 40px;
  background-color: #f6f8fc;
  padding-left: 14px;
}

.search-content {
  padding: 0 14px;

  .search-left {
    padding: 20px 0;
    min-width: 500px;
  }

  .search-right {
    padding: 20px 0;
    min-width: 558px;
  }
  .search-noadd {
    min-width: 448px;
  }
}

.pagination-container {
  text-align: center;
}

.red-text {
  @include fc(14px, #E7487E);
  cursor: pointer;
}

.red-text:nth-child(even) {
  padding-right: 10px;
  padding-bottom: 10px;
}

.blue-text {
  @include fc(14px, #4877e7);
  cursor: pointer;

  a {
    @include fc(14px, #4877e7);
  }
}
.table-content {
  position: relative;
  padding-left: 14px;
  padding-right: 14px;

  table {
    img {
      @include wh(60px, 60px);
      vertical-align: middle;
      margin: 10px 0;
    }
  }
}
.delete-dialog {
  .dialog-title {
    @include wh(540px, 4px);
    background-color: #5a8cff;
    line-height: 4px;
  }

  .dialog-content {
    padding: 80px 75px 50px;

    .delete-tip {
      margin-bottom: 60px;

      .delete-img {
        margin-left: 80px;
        display: inline-block;
        @include wh(50px, 50px);
        img {
          @include wh(50px, 50px);
        }
      }

      .delete-text {
        display: inline-block;
        padding-bottom: 15px;
        padding-left: 20px;
        vertical-align: bottom;
        @include fc(14px, #666);
      }
    }
    .button-group {
      margin-top: 40px;

      div {
        @include wh(160px, 40px);
        border-radius: 4px;
        border: 1px solid #5a8cff;
        color: #5a8cff;
        line-height: 40px;
        text-align: center;
        cursor: pointer;
      }
      .ok-btn {
        margin-right: 60px;
        background-color: #5a8cff;
        color: #fff;
      }
    }
  }
}
.data-empty {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fff;
  .img {
    @include wh(200px,200px);
    img {
      @include wh(200px, 200px);
    }
  }
  .text {
    @include fc(14px, #9b9b9b);
    width: 200px;
    text-align: center;
    margin-top: 20px;
  }
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

#good-table {
  .search-content {
    font-size: 0;

    .search-name {
      .el-input {
        font-size: 14px;
        width: inherit;

        input {
          @include wh(180px, 34px);
          border-radius: 0;
        }
      }
    }

    .el-select {
      width: 180px;
      height: 34px;
      margin: 0 20px;

      .el-input {
        font-size: 14px;

        input {
          border-radius: 0;
          height: 34px;
        }
      }
    }

    .el-button {
      @include wh(100px, 34px);
      border: 1px solid #4877e7;
      color: #4877e7;
      border-radius: 0;
      font-size: 14px;
      cursor: pointer;
    }
    .el-button.red {
      color: #e7487e;
      border-color: #e7487e;
    }
  }

  .el-table {
    width: inherit;
  }
  .el-table td {
    height: 80px;
  }

  .el-table .cell {
    padding-right: 0;
    padding-left: 0;
  }
  .el-select-dropdown {
    min-width: 181px !important;
  }
  .el-pagination {
    padding-top: 50px;
    padding-bottom: 14px;
  }
  .el-pagination__jump {
    margin-left: 5px;
    margin-right: 15px;
  }
  .el-dialog__header {
    padding: 0;
  }
  .el-dialog__body {
    padding: 0;
  }
}
</style>







