<template>
  <div class="preview-dialog-table">
        <el-dialog class="preview-dialog-editor" 
        :visible.sync="isPreviewVisible"
        :show-close="false"
        :before-close="handleClose"
        size=""
        top="19%">
          <div class="dialog-content">
            <div class="preview-content">
              <div class="phone-title clearfix">
                <div class="close left">
                  <i class="el-icon-close"></i>
                </div>
                <div class="text left">{{ previewObj.shopName }}</div>
                <div class="more right">
                  <i class="el-icon-more"></i>
                </div>
              </div>
              <div class="preview-title">
                <div class="back left">
                  <i class="el-icon-arrow-left"></i>
                </div>
                <div class="text left">菜品详情</div>
                <!-- <div class="more right">
                  <i class="iconfont icon-shop-fill"></i>
                </div> -->
              </div>
              <div class="preview-picture">
                <img :src="base_url + '/img_get.php?img=1&imgname='+ previewObj.previewPicture" alt="菜品图片">
              </div>
              <div class="preview-nameinfo">
                <div class="name-content clearfix">
                  <div class="name left">
                    <div class="title" v-if="previewObj.foodName === ''">菜品名</div>
                    <div class="title" v-else>{{ previewObj.foodName }}</div>
                    <div class="detail">
                      <span>月售 100</span>
                      <span>好评率 100%</span>
                    </div>
                  </div>
                  <div class="icon right">
                    <img src="../view/images/priase.png">
                    <img src="../view/images/share.png">
                    <img src="../view/images/heart.png">
                  </div>
                </div>
                <div class="priceicon-content">
                  <div v-if="previewObj.priceusing === priceType.DISCOUNT" class="price-icon">促销价</div>
                  <div v-if="previewObj.priceusing === priceType.FESTIVAL" class="price-icon">节日价</div>
                  <div v-if="previewObj.priceusing === priceType.VIP" class="price-icon">会员价</div>
                </div>
                <div class="price-content clearfix">
                  <div class="price left">
                    <span class="price-first">&yen;{{previewObj.priceFirst}}</span>
                    <span v-if="previewObj.priceSecond !== ''" class="price-second">&yen;{{previewObj.priceSecond}}</span>
                  </div>
                  <div class="cart right">
                    加入购物车
                    <!-- <img src="../view/images/shopcart.png" alt=""> -->
                  </div>
                </div>
              </div> 
              <div class="border"></div>
              <div class="preview-goodinfo">
                <div class="goodinfo-title">商品信息</div>
                <div class="goodinfo-composition">
                  <span>食材：</span>
                  <span v-for="item in previewObj.composition" :key="item">{{item}} </span>
                </div>
                <div class="goodinfo-feature">
                  <span>特色：</span>
                  <span v-for="item in previewObj.feature" :key="item">{{item}} </span>
                </div>
                <div class="goodinfo-like">
                  <span>点赞：</span>
                  <span>0</span>
                </div>
              </div>
              <div class="preview-remark">
                <div class="remark-title">
                  商品评价
                </div>
                <div class="remark-border"></div>
                <div class="remark-content">
                  <p>暂无评价</p>
                  <p>赶快去说说你的建议吧！</p>
                </div>
              </div>
            </div>
          </div>
        </el-dialog>
  </div>
</template>

<script>
import { priceType } from "@/config/cfg";

export default {
  props: {
    previewVisible: {
      type: Boolean,
      default: false
    },
    previewObj: {
      type: Object
    }
  },
  data() {
    return {
      isPreviewVisible: false,

      base_url: base_url,
      priceType: priceType
    };
  },
  watch: {
    previewVisible: function(val) {
      if (val === true) {
        this.isPreviewVisible = true;
      }
    }
  },
  methods: {
    handleClose(done) {
      this.$emit("on-close");
      this.isPreviewVisible = false;
    }
  }
};
</script>

<style lang="scss" scoped>
@import "src/styles/mixin.scss";

.preview-dialog-editor {
  .dialog-content {
    @include wh(300px, 606px);
    background-image: url(../view/images/telephone.png);
    background-repeat: no-repeat;
    background-size: contain;
    position: relative;

    .preview-content {
      @include wh(259px, 461px);
      background-color: rgb(241, 241, 241);
      position: absolute;
      left: 20px;
      top: 73px;
    }
  }
}
.preview-content {
  .phone-title {
    @include wh(100%, 30px);
    color: #fff;
    background-color: rgb(46, 46, 46);
    padding: 0 14px;
    line-height: 30px;
    .text {
      margin: 0 18px;
      font-size: 16px;
    }
    .more .el-icon-more {
      transform: rotate(90deg);
    }
  }
  .preview-title {
    @include wh(100%, 27px);
    color: #393939;
    background-color: #fff;
    line-height: 27px;
    padding: 0 14px;
    box-shadow: 0 1px 1px 0 #7d3901;

    .text {
      width: 163px;
      margin: 0 18px;
      text-align: center;
    }
  }
  .preview-picture {
    background-color: #fff;
    @include wh(100%, 80px);
    img {
      @include wh(100%, 80px);
    }
  }
  .preview-nameinfo {
    background-color: #fff;
    // @include wh(100%, 80px);
    padding: 5px 14px 10px 14px;
    .name-content {
      .name {
        .title {
          font-weight: bold;
        }
        .detail {
          @include fc(12px, #8a8a8a);
        }
      }
      .icon {
        img {
          @include wh(20px, 20px);
          padding-right: 5px;
        }
        img:last-child {
          padding-right: 0px;
        }
      }
    }
    .priceicon-content {
      height: 26px;
      padding-top: 5px;
    }
    .price-content {
      .price {
        .price-first {
          color: #ff3637;
          font-weight: bold;
          font-size: 16px;
        }
        .price-second {
          color: #8a8a8a;
          font-size: 12px;
        }
      }
      .cart {
        @include fc(12px, #fff);
        @include wh(92px, 20px);
        line-height: 20px;
        padding-left: 24px;
        background-image: url(../view/images/shopcart.png);
        background-repeat: no-repeat;
        background-size: contain;
        img {
          @include wh(65px, 16px);
        }
      }
    }
  }
  .preview-goodinfo {
    background-color: #fff;
    // @include wh(100%, 100px);
    @include fc(12px, #393939);
    padding: 14px;

    .goodinfo-title {
      font-size: 14px;
      margin-bottom: 5px;
    }
  }
  .preview-remark {
    background-color: #fff;
    margin-top: 10px;
    padding: 0 10px;

    .remark-border {
      border-bottom: 1px solid #ccc;
    }
    .remark-title {
      height: 27px;
      line-height: 27px;
      font-weight: bold;
      padding: 0 4px;
    }
    .remark-content {
      text-align: center;
      padding: 17px 0 20px 0;
      color: #8a8a8a;
    }
  }
  .border {
    margin: 0 10px;
    border-bottom: 1px solid #ccc;
  }
}
.price-icon {
  border: 1px solid #ff6f06;
  background-color: rgb(255, 247, 240);
  display: inline-block;
  font-size: 12px;
  padding: 0 2px;
  color: #ff6f06;
  border-radius: 2px;
}
</style>

<style lang="scss">
@import "src/styles/mixin.scss";

.preview-dialog-table {
  .preview-dialog-editor {
    .el-dialog {
      background: none;
      box-shadow: none;

      .el-dialog__body {
        padding: 0 30px;
      }
    }
  }
}
</style>





